
<template>
    <div class="appBox">
        <div class="header">
            <img src="@/static/images/modify/bg_iamge.png" alt />
        </div>
        <div class="activeRule">
            <div class="title"><i class="leftImages"></i>用户福利<i class="rightImages"></i></div>
            <p>首次注册就送100元的MCT平台币！创建红包群再送100元的MCT平台币！注：MCT是millioncoin.name的平台币，未来MCT将会在millioncoin.name平台中起到空投糖果所得权、平台股份的持币分红权等应用，millioncoin.name也将给MCT更多赋能，让MCT有更多的价值落地。</p>
        </div>
        <div class="activeRule">
            <div class="title"><i class="leftImages"></i>三级邀请奖励<i class="rightImages"></i></div>
            <p>邀请您的朋友加入，您可拿到一级用户收益的6%、二级用户收益的5%、三级用户收益的4%，您的朋友抢红包、当群主、还是玩财富树，您都有以上收益！</p>
        </div>
        <div class="main">
            <p class="title"><i class="leftImages"></i>我的邀请码<i class="rightImages"></i></p>
            <p class="code" id="invCode">{{inviteInfo.invite_code}}</p>
            <button class="copy" @click="copyInvite(inviteInfo.invite_code)">复制</button>
            <hr />
            <img :src="inviteInfo.invite_qrcode"alt />
            <p class="bottom">扫描二维码，马上注册抢红包！</p>
        </div>
    </div>
</template>

<script>
    import { inviteFriend } from '@/api/user'
    import Clipboard from 'clipboard'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Invite",
        data()
        {
            return {

                inviteInfo:{
                    invite_code:'',
                    invite_qrcode:''
                }
            }
        },
        mounted()
        {
            this.getInviteInfo()
        },
        methods:{
            // 获取邀请信息
            getInviteInfo()
            {
                inviteFriend().then(res => {
                    this.inviteInfo = res
                })
            },
            // 复制邀请码
            copyInvite(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    Toast.success('复制成功');
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    Toast.fail('当前浏览器不支持');
                    // 释放内存
                    clipboard.destroy()
                })

            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/mineShare.css";
</style>



